import React from "react";
import {connect} from "react-redux";
import {registerOneTimePriceCMSConf} from "eshop/modules/simfree/actions/metaData";
import {selectOneTimePrice} from "eshop/modules/simfree/actions/filter";
import {getTieredOneTimePriceForCurrentOfferType,getSelectedOneTimePrices} from "eshop/modules/simfree/selectors";
import ProductListOneTimeFilterView from "./view/ProductListOneTimeFilterView"
import {filterLabelsHardcodedAsGross} from "eshop/modules/simfree/selectors";
import OnlineUtils from "eshop/utils/OnlineUtils";

class ProductListOneTimeFilterComponent extends React.Component {

    constructor(props) {
        super(props);
        this.props.setCmsConf(this.props.tieredPriceConf);
        this.labelProvider = this.labelProvider.bind(this)
    }

    labelProvider(row){
        let to = this.props.filterLabelsHardcodedAsGross ? OnlineUtils.formatPrice(OnlineUtils.toGrossHardcoded(row.to)) : row.to
        let from = this.props.filterLabelsHardcodedAsGross ? OnlineUtils.formatPrice(OnlineUtils.toGrossHardcoded(row.from)) : row.from
        return from + " zł - " + to + " zł"
    }

    render() {
        return (
            <ProductListOneTimeFilterView
                tieredPrices={this.props.tieredPrices}
                header = {this.props.title}
                type = "checkbox"
                id="expander-module-one-time"
                labelFormatter = {this.labelProvider}
                selectedTieredPrices={this.props.selectedTieredPrices}
                onClick={this.props.onClick}
                showExpanderNumber={this.props.tieredPriceNumberExpander}
                showComponent={this.props.tieredPrices && this.props.tieredPrices.length > 0}
                initiallyExpanded={this.props.initiallyExpanded}
                />
        );
    }
}

const mapStateToProps = state => ({
    tieredPrices : getTieredOneTimePriceForCurrentOfferType(state),
    selectedTieredPrices : getSelectedOneTimePrices(state),
    filterLabelsHardcodedAsGross: filterLabelsHardcodedAsGross(state)
});

const mapDispatchToProps = (dispatch) => ({
    setCmsConf: (value) => dispatch(registerOneTimePriceCMSConf(value)),
    onClick: (id) => dispatch(selectOneTimePrice(id))
});

export default ProductListOneTimeFilterComponent = connect(mapStateToProps, mapDispatchToProps)(ProductListOneTimeFilterComponent);

